/* App.css */

.app-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

/* Desktop: Always show the Import/Export container in the top right */
.export-import-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  display: flex;
  gap: 10px;
}

/* Desktop: Hide the mobile toggle button */
.import-export-toggle {
  display: none;
}

.export-import-container button {
  padding: 10px 15px;
  font-size: 0.9rem;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.export-import-container .export-btn {
  background-color: #28a745;
  color: #fff;
}

.export-import-container .export-btn:hover {
  background-color: #218838;
}

.export-import-container .import-btn {
  background-color: #17a2b8;
  color: #fff;
}

.export-import-container .import-btn:hover {
  background-color: #138496;
}

/* Sidebar styling */
.sidebar {
  width: 300px;
  padding: 20px;
  background: #f7f7f7;
  border-right: 1px solid #ddd;
  overflow-y: auto;
}

.sidebar-header {
  margin-bottom: 20px;
}

.sidebar-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #333;
}

/* Control Sections */
.control-section {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
}

.control-section h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: #555;
}

/* Control groups for labels and inputs */
.control-group {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.control-group label {
  width: 100px;
  font-size: 0.9rem;
  color: #333;
}

.control-group input,
.control-group select {
  flex: 1;
  padding: 5px;
  font-size: 0.9rem;
  border: 1px solid #ccc;
  border-radius: 3px;
}

/* Actions button within sidebar */
.control-section.actions {
  display: flex;
  justify-content: space-between;
}

.control-section.actions button {
  flex: 1;
  padding: 8px;
  font-size: 0.9rem;
  border: none;
  border-radius: 3px;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

/* Canvas container styling */
.canvas-container {
  flex: 1;
  height: 100vh;
  background: #eaeaea;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
    overflow: hidden;
    /* Prevent scrolling */
    height: 100vh;
  }

  /* Sidebar becomes the top half (50% height) */
  .sidebar {
    width: 100%;
    height: 50vh;
    border-right: none;
    border-bottom: 1px solid #ddd;
    padding: 15px;
    overflow-y: auto;
  }

  /* Canvas takes the bottom half (50% height) */
  .canvas-container {
    width: 100%;
    height: 50vh;
    background: #eaeaea;
  }

  /* Increase inputs size for easier tapping */
  .control-group input,
  .control-group select {
    padding: 10px;
    font-size: 1rem;
  }

  .control-group label {
    font-size: 1rem;
  }

  /* Hide the export/import container by default on mobile */
  .export-import-container {
    display: none;
    position: fixed;
    top: 20px;
    right: 20px;
    flex-direction: column;
    gap: 5px;
  }

  .export-import-container.active {
    display: flex;
  }

  /* Show a toggle button for Import/Export on mobile */
  .import-export-toggle {
    display: block;
    position: fixed;
    top: 20px;
    right: 20px;
    background: #17a2b8;
    color: #fff;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-size: 1.5rem;
    line-height: 40px;
    text-align: center;
    z-index: 1100;
    cursor: pointer;
  }
}